  }
  100% {
    transform: rotateZ(-270deg);
  }
} */

@media (max-width: 992px) {
  .wrap {
    width: 500px;
  }
  .img {
    width: 70px;
    height: 70px;
  }
  .img-wrap {
    width: 70px;
  }
  .text {
    margin: 15px 0;
    /* line-height: 1.2; */
    text-align: center;
    font-size: 14px;
  }
  .rate-name {
    margin: 0px;
  }
}
@media (max-width: 630px) {
  .wrap {
    max-width: 400px;
  }
  .img {
    width: 60px;
    height: 60px;
  }
  .img-wrap {
    width: 60px;
  }
  .text {
    margin: 10px 0;
    text-align: center;
    font-size: 11px;
  }
  .rate-name {
    margin: 10px;
    font-size: 18px;
  }
  .rate-stars svg {
    width: 20px;
    height: 20px;
  }
}
@media (max-width: 500px) {
  .wrap {
    max-width: 280px;
  }
  .img {
    width: 50px;
    height: 50px;
  }
  .img-wrap {
    width: 50px;
  }
  .text {
    font-weight: 300;
    font-size: 9px;
    line-height: 10px;
    text-align: center;
    margin: 5px 0;
  }
  .rate-name {
    margin: 0px;
    font-size: 16px;
  }
  .name {
    top: -50%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 18px;
  }
  .rate-stars svg {
    width: 16px;
    height: 16px;
  }
  .rate-stars {
    margin: 5px;
  }
  .arrows {
    cursor: pointer;
    position: absolute;
    top: 5%;
    border: 2px solid #44ccbb;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
  }
  .rate-reviews {
    font-weight: bold;
    font-size: 10px;
  }
}

.transition {
  -webkit-animation: 1s transitionText;
          animation: 1s transitionText;
}

@-webkit-keyframes transitionText {
  0% {opacity: 0}
  100% {opacity: 1}
}

@keyframes transitionText {
  0% {opacity: 0}
  100% {opacity: 1}
}

